/* eslint-disable no-unused-vars */
import React, { useEffect } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import { useForm, SubmitHandler } from 'react-hook-form'
import { ThemeProvider, Typography } from '@mui/material'
import { yupResolver } from '@hookform/resolvers/yup'
import { MoonLoader } from 'react-spinners'
import { Button, MenuProps } from '@material-ui/core'
import OnboardingSchema from '../../../../utils/validation-schemas/onboarding-schemas'
import getFormData from '../../../../utils/object-to-form-data'
import { formatApiDate } from '../../../../utils/dates'
import useStyles from './OnboardingFormContacts.styles'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { AllOnboardingInfo, ContactsInfo } from '../../../../services/types/onboarding/onboarding-types'
import { postOnboarding } from '../../../../services/axios/auth'
import ControlledSocialContact from '../../../Sub/FormElements/controlled-social-contact/controlled-social-contact'

import theme from '../../../../utils/themeMUI5'

const OnboardingFormContacts = () => {
  const classes = useStyles()
  const navigate = useNavigate()
  const location = useLocation()
  const dispatch = useAppDispatch()

  const avatar = location.state?.avatar
  const files: Array<any> = location.state?.files
  const { postOnboardingRequest } = useAppSelector((store) => store.auth)
  const personalInfo = JSON.parse(localStorage.getItem('personalInfo') as string)
  const experienceInfo = JSON.parse(localStorage.getItem('experienceInfo') as string)
  const projectsInfo = JSON.parse(localStorage.getItem('projectsInfo') as string)
  const resumeInfo = JSON.parse(localStorage.getItem('resumeInfo') as string)
  const contactsInfo = JSON.parse(localStorage.getItem('contactsInfo') as string)

  const { profile } = useAppSelector((store) => store.auth)

  const defaultValues = contactsInfo || {
    telegram: '',
    email: '',
    whatsapp: '',
  }

  const {
    control,
    handleSubmit,
    register,
    setValue,
    watch,
    getValues,
    formState: { errors },
  } = useForm<ContactsInfo>({
    defaultValues,
    resolver: yupResolver(OnboardingSchema.onboardingContactsSchema),
    mode: 'onChange',
  })

  useEffect(() => {
    if (!personalInfo) {
      navigate('/onboarding/personal-info')
    }
  }, [personalInfo])

  useEffect(() => {
    if (profile) {
      localStorage.removeItem('personalInfo')
      localStorage.removeItem('experienceInfo')
      localStorage.removeItem('resumeInfo')
      localStorage.removeItem('contactsInfo')
      localStorage.removeItem('projectsInfo')
      navigate('/diary')
    }
  }, [profile])

  const onSubmit: SubmitHandler<ContactsInfo> = (formData) => {
    if (!personalInfo || !experienceInfo) return
    localStorage.setItem('contactsInfo', JSON.stringify(formData))
    const { course_id, ...restExperienceInfo } = experienceInfo

    const contacts = []

    if (formData?.email) contacts.push({ type: 2, value: formData.email })
    if (formData?.whatsapp) contacts.push({ type: 3, value: formData.whatsapp })

    const allOnboardingData: AllOnboardingInfo = {
      ...personalInfo,
      ...restExperienceInfo,
      ...projectsInfo,
      ...resumeInfo,
      birth_date: formatApiDate(new Date(personalInfo.birth_date)),
      course: course_id?.id,
      image: avatar || undefined,
      city: personalInfo?.city?.id,
    }

    const allOnboardingDataFD = getFormData(allOnboardingData)

    files.forEach((file, index) => {
      allOnboardingDataFD.append(`resume[${index}]resume`, file, file.name)
    })

    let contactsIndex = 0

    if (formData?.telegram) {
      allOnboardingDataFD.append(`contacts[${contactsIndex}]type`, '1')
      allOnboardingDataFD.append(`contacts[${contactsIndex}]value`, formData?.telegram)
      contactsIndex += 1
    }

    if (formData?.email) {
      allOnboardingDataFD.append(`contacts[${contactsIndex}]type`, '2')
      allOnboardingDataFD.append(`contacts[${contactsIndex}]value`, formData?.email)
      contactsIndex += 1
    }

    if (formData?.whatsapp) {
      allOnboardingDataFD.append(`contacts[${contactsIndex}]type`, '3')
      allOnboardingDataFD.append(`contacts[${contactsIndex}]value`, formData?.whatsapp)
    }

    dispatch(postOnboarding(allOnboardingDataFD))
  }

  return (
    <ThemeProvider theme={theme}>
      {postOnboardingRequest.isError && (
        <Typography color="error">Что-то пошло не так, обновите страницу и попробуйте снова</Typography>
      )}
      <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
        <fieldset className={classes.fieldset}>
          <div className={classes.fieldContainer}>
            <Typography variant="body2" mb="17px">
              Контакты для связи
            </Typography>
            <ControlledSocialContact
              variant="telegram"
              width={387}
              control={control}
              error={errors?.telegram?.message || ''}
              name="telegram"
              placeholder="Введи ник"
            />
            <Typography variant="caption2" mt="-11px" mb="19px" sx={{ color: '#797981' }}>
              По статистике большая часть соискателей связывается через Telegram. По возможности указывайте его
            </Typography>
            <ControlledSocialContact
              variant="email"
              width={387}
              control={control}
              error={errors?.email?.message || ''}
              name="email"
              placeholder="Введи эл.адрес"
            />
            <ControlledSocialContact
              variant="whatsapp"
              width={387}
              control={control}
              error={errors?.whatsapp?.message || ''}
              name="whatsapp"
              placeholder="Введи номер"
            />
          </div>

          <div className={classes.buttonContainer}>
            <Button
              type="button"
              className={`${classes.formButton} ${classes.previousButton}`}
              onClick={() => {
                const values = getValues()
                // dispatch(setResumeInfo(values))
                localStorage.setItem('contactsInfo', JSON.stringify(values))
                navigate('/onboarding/resume', { state: { avatar } })
              }}
            >
              Назад
            </Button>
            {postOnboardingRequest.status === 'pending' ? (
              <MoonLoader color="#1d6bf3" loading size={39} />
            ) : (
              <Button className={classes.formButton} type="submit">
                Сохранить
              </Button>
            )}
          </div>
        </fieldset>
      </form>
    </ThemeProvider>
  )
}

export default OnboardingFormContacts
